<template>
  <div class="container mt-3">
    <div class="mt-3">
      <div class="mt-3">
        <h3> <i class="fas fa-user-friends"/> Contactos de {{this.NombreProveedor}}</h3>
      </div>

      <div>
            <b-row class="my-2">
                <b-col md="4">  
                  <b-form-group label="Filtrar"
                                label-cols-sm="1"
                                label-cols-md="2"
                                label-size="sm"
                                label-for="filterInput">
                        <b-input-group size="sm">
                            <b-form-input v-model="filter"
                                          type="search"
                                          id="filterInput"
                                          placeholder="Escribe el filtro..."
                                          autocomplete="off">
                            </b-form-input>
                            <b-input-group-append>
                                <b-button :disabled="!filter" @click="filter = ''">
                                    <i class="fas fa-times"></i>
                                </b-button>
                            </b-input-group-append>
                        </b-input-group>
                    </b-form-group>                
                </b-col>

                <b-col md="3">
                  <b-form-group label="Por página"
                                label-cols-sm="2"
                                label-cols-md="2"
                                label-cols-lg="4"
                                label-size="sm"
                                label-for="perPageSelect">
                    <b-form-select v-model="perPage"
                                    id="perPageSelect"
                                    size="sm"
                                    :options="pageOptions">
                    </b-form-select>
                    </b-form-group>
                </b-col>

                <b-col md="3" offset-md="2">
                    <b-button
                        @click="limpiarDatos()"
                        v-b-modal.modalRegistroContacto
                        variant="primary"
                        class="btn btn-sm py-1 mt-1 btn-block"
                        >
                        <i class="fa fa-plus"/> Nuevo
                    </b-button>
                 </b-col>
            </b-row>
            
            
            <b-table small
                    striped 
                    bordered
                    stacked="md"
                    head-variant="dark"
                    :items="items"
                    :fields="fields"
                    :current-page="currentPage"
                    :per-page="perPage"
                    :filter="filter"
                    :filterIncludedFields="filterOn"
                    @filtered="onFiltered"
                    >

                <template v-slot:cell(estatus)="row">
                  <div style="width: 15px"> 
                    <img id="imagen" src="https://img.icons8.com/color/48/000000/verified-badge.png" v-if=row.item.cBPrincipal />                
                  </div>     
                </template>

                <template v-slot:cell(acciones)="row">
                    <b-button 
                    size="sm"
                    class="mr-1"
                    v-b-tooltip.hover
                    variant="outline-info"
                    title="Ver detalle"
                    @click="cargarDatosContacto(row.item.idContacto)"
                    v-b-modal.modalInfoContacto>
                      <i class="fa fa-eye"/>
                    </b-button>
                    
                    <b-button 
                    size="sm"
                    class="mr-1"
                    v-b-tooltip.hover
                    variant="outline-info"
                    title="Editar"
                    @click="cargarDatosContacto(row.item.idContacto)"
                    v-b-modal.modalRegistroContacto>
                      <i class="fa fa-edit"/>
                    </b-button>

                    <b-button 
                    size="sm"
                    class="mr-1"
                    variant="danger"
                    title="Desactivar"
                    @click="desactivar(row.item.idContacto)"
                    >
                      <i class="fa fa-times"/>
                    </b-button>
                </template>

                <template v-slot:cell(contactar)="row">
                    <b-button
                        size="sm"
                        @click="llamar(row.item)"
                        class="mr-1"
                        variant="primary"
                        v-b-tooltip.hover
                        title="Llamar"
                    >
                        <i class="fas fa-phone-alt"/>
                    </b-button>

                    <b-button
                        size="sm"
                        @click="enviarWs(row.item)"
                        class="mr-1"
                        variant="success"
                        v-b-tooltip.hover
                        title="Whatsapp"
                    >
                        <i class="fab fa-whatsapp"/>
                    </b-button>

                    <b-button
                        size="sm"
                        @click="enviar(row.item)"
                        class="mr-1"
                        variant="info"
                        v-b-tooltip.hover
                        title="Enviar correo"
                    >
                        <i class="fas fa-envelope"/>
                    </b-button> 
                </template>
            </b-table>

            <b-pagination v-model="currentPage"
                          class="mt-1"
                          :total-rows="totalRows"
                          :per-page="perPage"
                          align="center"
                          aria-controls="my-table">
            </b-pagination>


        <!-- modal info contacto -->
        <b-modal
        id="modalInfoContacto"
        title="Detalles de contacto"
        hide-footer
        >
            <b-card border-variant="light">
                <b-container>
                    <b-row class="mb-2">
                        <b-col class="font-weight-bold" md=5> Nombre: </b-col>
                        <b-col md=7>{{this.form.Nombre}}</b-col>  
                    </b-row>

                    <b-row class="mb-2">
                        <b-col class="font-weight-bold" md=5> Teléfono: </b-col>
                        <b-col md=7> {{this.form.Telefono}} </b-col> 
                    </b-row>

                    <b-row class="mb-2">
                        <b-col class="font-weight-bold" md=5> Teléfono Movil: </b-col>
                        <b-col md=7> {{this.form.TelefonoMovil}} </b-col>  
                    </b-row>
                    
                    <b-row class="mb-2">
                        <b-col class="font-weight-bold" md=5> Correo: </b-col>
                        <b-col md=7> {{this.form.Correo}}
                        </b-col>
                    </b-row>
                 
                    <b-row class="mb-2">
                        <b-col class="font-weight-bold" md=5>  Principal: </b-col>
                        <b-col sm=7>
                            <i class="fas fa-check-square fa-2x text-primary" v-if="form.Principal"></i>
                            <i class="fas fa-window-close fa-2x text-danger" v-else></i>
                        </b-col>   
                    </b-row>
                   
                </b-container>
            </b-card>       
        </b-modal>

           <!-- Modal guardar contacto -->  

        <b-modal
        id="modalRegistroContacto"
        title="Registro contacto"
        centered
        @ok="handleOk"
        >
            <form ref="form" @submit.stop.prevent="guardarContacto">
               
                <label ><i class="fas fa-id-card-alt"/> Nombre de contacto <span class="text-danger">*</span></label>
                <b-form-input
                    id="Nombre"
                    v-model="form.Nombre" 
                    autocomplete="off"
                />
                
                <label class="mt-3" ><i class="fas fa-phone-alt"/> Teléfono </label>
                <b-form-input
                    id="Nombre"
                    v-model="form.Telefono"  
                    autocomplete="off"
                    type="number"
                />

                <label class="mt-3" ><i class="fas fa-mobile-alt"/> Teléfono Móvil </label>
                <b-form-input
                    id="Nombre"
                    v-model="form.TelefonoMovil"  
                    autocomplete="off"
                    type="number"
                />

                <label class="mt-3" ><i class="far fa-envelope-open"/> Correo </label>
                <b-form-input
                    id="Nombre"
                    v-model="form.Correo" 
                    autocomplete="off"
                />

                <b-form-checkbox class="mt-3" v-model="form.Principal" >
                Cuenta Principal
                </b-form-checkbox>
            </form>    
        </b-modal> 

        </div>
    </div>
  </div>
</template>

<script>
import servicioCuentas from "@/services/servicio-cuentas";
import servicioProveedores from "@/services/servicio-proveedores"
export default {
  data: function() {
    return {
      rutaNuevo: "proveedores/registro/",
      NombreProveedor: "",
      bancos: [{ value:null, disabled: true, text: "seleccione un banco"}],
      items: [],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15],
      filter: null,
      filterOn: [],
      contacto:{
        IdContacto: 0,
        Nombre:'',
        Telefono: '',
        TelefonoMovil: '',
        Correo: '',
        Principal: false
      },
      form:{
        IdContacto: 0,
        Nombre:'',
        Telefono: '',
        TelefonoMovil: '',
        Correo: '',
        Principal: false        
      },
      fields: [
        {
          key: "estatus",
          label: "  "
        },
        {
          key: "nombre",
          label: "Nombre",
          sortable: true,
          class: "text-center",
          sortDirection: "desc",
        },
        {
          key: "telefono",
          label: "Teléfono",
          sortable: true,
          class: "text-center",
        },
         {
          key: "telefonoMovil",
          label: "Teléfono Móvil",
          sortable: true,
          class: "text-center",
        },
        
         {
          key: "correo",
          label: "Correo",
          sortable: true,
          class: "text-center",
        },
        {
          key: "contactar",
          label: "Contactar",
          sortable: false,
          class: "text-center"
        },
        {
          key: "acciones",
          label: "Acciones",
          sortable: false,
          class: "text-center"
        },
      ],
      permisoEscritura: true,
    };
  },
  components: {
  //  TableSortable,
  },
  async mounted() {
    var id = this.$route.params.id
    await this.consultarContactos(id);
    await this.consultarProveedor(id);
    await this.CargarRenglones();
    this.cargaBancosNombres();
    

  },
  computed:{
    sortOptions() {
      // Create an options list from our fields
      return this.fields
                  .filter((f) => f.sortable)
                  .map((f) => {
                      return { text: f.label, value: f.key };
                  });
      },
  },
  methods: {
    CargarRenglones() {
          setTimeout(() => {
              this.totalRows = this.items.length;
              console.log(this.totalRows)
          }, 1000);
    },
     onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },
      desactivar(id){
      let contacto = {
        idContacto : id,
        activado : 0
      }
      this.$swal.fire({
        title: "¿Desea eliminar el contacto?",
        text: "se perderan los datos del contacto",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Si",
        cancelButtonText: "No",
        // backdrop: `
        //             rgba(101, 101, 236 ,0.3)
            
        //         `
      }).then((result) => {
        if (result.value){
          servicioProveedores.DesactivarContacto(contacto).then((r) => {
            this.$swal.fire({
                text: r.message,
                icon: "success",
                showConfirmButton: false
                
            });
            let this_=this;
            setTimeout(function(){this_.$router.go()}, 1000)
          });
        }
      })
    },

    enviarWs(item) {
      if (item.telefonoMovil){
       window.open(`https://api.whatsapp.com/send?phone=52${item.telefonoMovil}`, "_blank");
      }else
       this.$swal.fire({
                    text: "No existe teléfono móvil asociado",
                    icon: "warning"
                })
    },
    llamar(item) {
      if (item.telefono){
        window.open(`tel:+52${item.telefono}`, "_blank");
      }else
        if(item.telefonoMovil){
          window.open(`tel:+52${item.telefonoMovil}`, "_blank");
        }
        else{
           this.$swal.fire({
                    text: "No existe telefono asociado",
                    icon: "warning"
                })
        }
      
    },
    enviar(item){
      if(item.correo){
        window.open(`mailto:${item.correo}`)
      }else{
        this.$swal.fire({
          text: "No existe correo asociado",
          icon: "warning"
        })
      }
    }, 

    redirect() {
            setTimeout(() => {
                this.$router.push({ path: `${this.rutaNuevo}0` });
            }, 500);
        },
   
    async consultarProveedor(id){
        this.$loading(true);
        await servicioProveedores.proveedores(id)
        .then((r) => {
            console.log(r.data.proveedor[0].nombreComercial)
            this.NombreProveedor = r.data.proveedor[0].nombreComercial;
            this.$loading(false);
        });
    },

    handleOk(bvModalEvt) {
        // Prevent modal from closing
        bvModalEvt.preventDefault()
        // Trigger submit handler
        this.guardarContacto()
    },

    guardarContacto(){
        let contacto = {
            idContacto: this.form.IdContacto,
            idProveedor: this.$route.params.id,
            nombre: this.form.Nombre,
            telefono: this.form.Telefono,
            telefonoMovil: this.form.TelefonoMovil,
            correo: this.form.Correo,
            cBPrincipal: this.form.Principal
        } 
        console.log(contacto)
        if(contacto.telefono || contacto.telefonoMovil || contacto.correo){
            servicioProveedores.ContactoGuardar(contacto).then((r) => {
                        console.log(r);
                        if(r.value){
                            this.$swal.fire({
                                text: r.message,
                                icon: "success"
                            });

                            let this_=this;
                            setTimeout(function(){this_.$router.go()}, 1000)
                        
                        }else {
                            this.$swal.fire({
                                text: r.message,
                                icon: "warning"
                            });
                        }
                    });
        }else{
            this.$swal.fire({
                text: "por lo menos proporcione un dato para el contacto",
                icon: "warning"
            })
        }
      
    },

    limpiarDatos(){
        this.form.IdContacto = 0
        this.form.Nombre = ""
        this.form.Telefono = ""
        this.form.TelefonoMovil = ""
        this.form.Correo = ""
        this.form.Principal = false
    },

    async cargarDatosContacto(id){
      this.$loading(true);
      await servicioProveedores.Contacto(id)
        .then((r) => {
          this.CargarRenglones();
          console.log(r.data.contacto[0])
          this.form.IdContacto = r.data.contacto[0].idContacto
          this.form.Nombre = r.data.contacto[0].nombre
          this.form.Telefono = r.data.contacto[0].telefono
          this.form.TelefonoMovil = r.data.contacto[0].telefonoMovil
          this.form.Correo = r.data.contacto[0].correo
          this.form.Principal = r.data.contacto[0].cBPrincipal
          this.$loading(false);
        })
    },

    async consultarContactos(id) {
      this.$loading(true);
     await servicioProveedores.contactos(id)
        .then((r) => {
          console.log(r)
          this.items = r.data.contacto;
          this.$loading(false);
        })
        .catch((error) => {
          console.log(error);
          this.$loading(false);
        });
    },
    cargaBancosNombres(){
      this.$loading(true);
        servicioCuentas.NombreBancos().then((r) => {
          r.data.bancos.forEach((x) => {
            this.bancos.push({ value: x.idBanco, text: x.nombre});
            this.$loading(false);
         })
        })
    },
  },
};
</script>
<style scoped>
</style>
<style>
.hidden_header {
  display: none;
}
#imagen {
    display: block;
    margin: 0 auto;
    width:30px;
}
</style>